const SalesPage = () => {
    return (<section className='price'>
        <div>
            <h1 className='h1sales'>promotion!</h1>
        </div>
        <div className="promo">
            <p>Window cleaning is free when ordering office cleaning.</p>
        </div>
        <div className="imgPromo">
            <img src={imgSzyba} alt="mycie szyby" />
            <img src={imgSzyba2} alt="sciągaczka do szyb" />
        </div>
        <div className="promo">
            <p>30% off with a loyal customer card</p>
        </div>
        <div className="imgPromo">
            <img src={imgKarta} alt="Karta stałego klienta" />
        </div>
    </section>
    );
}
const PriceListPage = () => {
    return (<section className='price'>
        <div>
            <h1 className='h1price'>Price list</h1>
        </div>
        <div className='priceList'>
            <table>
                <thead>
                    <tr>
                        <th>quantity/type</th>
                        <th>Small package</th>
                        <th>Normal package</th>
                        <th>Full package</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>1</td>
                        <td>15 Euros</td>
                        <td>20 Euros</td>
                        <td>25 Euros</td>
                    </tr>
                    <tr>
                        <td>5</td>
                        <td>70 Euros</td>
                        <td>90 Euros</td>
                        <td>110 Euros</td>
                    </tr>
                    <tr>
                        <td>10</td>
                        <td>135 Euros</td>
                        <td>170 Euros</td>
                        <td>200 Euros</td>
                    </tr>
                </tbody>
            </table>
            <p style={{ padding: '10px' }}>Individual pricing for larger quantities</p>
        </div>
    </section >);
}
const ContactPage = () => {
    return (
        <section className="contact">
            <div>
                <h1 className='h1contact'>Contact us</h1>
            </div>
            <div className="contact">
                <div>
                    <img src={logo} alt="logo" />
                    <p>Chuck Norris</p>
                    <p>Cleaners</p>
                </div>
                <div className='icons'>
                    <p><i className="fas fa-phone" alt='telefon'></i> 100-200-300</p>
                    <p><i className="far fa-envelope"></i> <a href='a'
                        alt='wiadomość'>cleaners@cleaners.com</a></p>
                    <p><i className="fab fa-facebook" alt='facebook'></i><a href="/">Facebook</a>
                    </p>
                </div>
            </div>
        </section >
    );
}
const ErrorPage = () => {
    return (<section className='price'>
        <div>
            <h1 className='h1sales'>this page does not exist</h1>
        </div>
    </section>);
}

const Page = () => {
    return (
        <>
            <Switch>
                <Route path='/GithubPage/pages/card' exact component={StartPage} />
                <Route path='/GithubPage/pages/card/about' exact component={AboutPage} />
                <Route path='/GithubPage/pages/card/services' exact component={ServicesPage} />
                <Route path='/GithubPage/pages/card/sales' exact component={SalesPage} />
                <Route path='/GithubPage/pages/card/priceList' exact component={PriceListPage} />
                <Route path='/GithubPage/pages/card/contact' exact component={ContactPage} />
                <Route component={ErrorPage} />
            </Switch>
        </>
    );
}

export default Page;