import React from 'react';
import { NavLink } from 'react-router-dom';
const Navigation = () => {
    const navItems = [
        {
            id: 0,
            name: 'Home',
            path: '/GithubPage/pages/card',
            exact: true
        },
        {
            id: 1,
            name: 'About',
            path: '/GithubPage/pages/card/about',
        },
        {
            id: 2,
            name: 'Services',
            path: '/GithubPage/pages/card/services',
        },
        {
            id: 3,
            name: 'Bonus',
            path: '/GithubPage/pages/card/sales',
        },
        {
            id: 4,
            name: 'Price List',
            path: '/GithubPage/pages/card/priceList',
        },
        {
            id: 5,
            name: 'Contact',
            path: '/GithubPage/pages/card/contact',
        },
    ];
    const menu = navItems.map(item => (
        <li key={item.id}><NavLink to={item.path} exact={item.exact ? true : false} >{item.name}</NavLink></li>
    ))
    return (
        <>
            <nav>
                <ul>
                    {menu}
                </ul>
            </nav>
        </>
    );
}

export default Navigation;